import uParse from  "../../components/uni-rich/parse.vue"
export default {
  components: {
    uParse
  },
  data() {
    return {
      navList: ['商品', '评价', '详情', '推荐'],   // 顶部导航栏
      goods_id: '',                         // 商品ID
      navIndex: 0,                          // 顶部初始索引值
      top: 0,                               // 滚动距离顶部距离
      indicatorDots: true,                  // 指示点显隐
      autoplay: true,                       // 自动轮播
      interval: 3000,                       // 自动轮播时间 
      duration: 2000,                       // 轮播过渡时间
      indicatorActiveColor: '#fff',         // 轮播图指示点选中颜色
      swiperList:[],                        // 轮播元素数组
      isShowTop: true,                      // 顶部
      detail: {},                           // 商品详情信息
      specData: {},                         // 商品规格
      select_arr: [],                       // 选中商品id数组
      select_name: [],                      // 选中的规格名称
      spec_sku_id: '',                      // 选中商品id拼接信息  例子：10028_10253_10256
      goods: {},                            // 选中规格商品价格
      strings: '',                          // 商品详情信息
      goods_num: 1,                         // 购买商品数量
      isCar: false,                         // 是否点击加入购物车
      comment_data: [],                     // 评论内容
      comment_data_count: '',               // 评论总数量
      
      // data: {
      //   price: 1099,
      //   textList: ['包邮', '自营'],
      //   title: '',                             
      // },                                     // 商品价格等
      // store: {
      //   name: 'SONY官方自营店',
      //   imgUrl: ''   
      // },                                      // 店名头像信息
      showPanic: false,                       // 顶部分享显示与隐藏
      coverShow: false,                       // 全局遮罩层显隐
      // sale_info: [{
      //   title: '满送',
      //   info: '满999元送4000毫安的充电宝,购买后送200积分',
      //   type: 1
      // }, {
      //   title: '促销',
      //   info: '满1548元，省150元',
      //   time: '2019.06.12-2019.06.15',
      //   type: 2
      // }],
      normShow: false,                        // 商品规格弹窗
    }
  },
  // 接受首页传递的参数
  onLoad(option) {
    console.log('分享文章详情页接受到的参数',option)
    this.goods_id = option.goods_id
    if(option.panic === 'true') {
      this.showPanic = true
      return
    }
    
    // 获取商品详情
    this.getDetail()
  },
  methods: {
    
    // 获取商品详情
    getDetail() {
      let that = this
      this.$http({
        url: this.$api.goods_detail,
        data: {
          goods_id: this.goods_id
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log(res.data.detail)
            that.detail = res.data.detail
            that.comment_data = that.detail.comment_data
            that.comment_data_count = that.detail.comment_data_count
            that.swiperList = that.detail.image
            that.specData = res.data.specData
            // that.spec_sku_id = that.specData.spec_attr[0].spec_items[0].item_id + '_' + 
            
            if(that.specData) {
              let obj = {}
              obj.goods_price = that.specData.spec_list[0].form.goods_price
              // 第一个规格第一个商品没图片默认是轮播图第一张
              if(that.specData.spec_list[0].form.image_path) {
                obj.image_path = that.specData.spec_list[0].form.image_path
              } else {
                obj.image_path = that.swiperList[0].file_path
              }
              obj.stock_num = that.specData.spec_list[0].form.stock_num
              
              that.goods = obj
               // 默认选中的规格，以及规格名称
              that.specData.spec_attr.map((item, index) => {
                that.select_arr.push(item.spec_items[0].item_id)
                that.select_name.push(item.spec_items[0].spec_value)
              })  
            }
            
            
            
          } else if(res.code === 0 || res.code === -1 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '商品详情加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 商品规格
    selectNorm(shu, li, num) {
      console.log(shu, li , num)
      this.select_arr[shu] = li.item_id
      let arr = []
      let arr2 = []
      
      // 选中的规格id数组
      this.select_arr.map((item, index) => {
        if(index === shu) {
          arr.push(li.item_id)
        } else {
          arr.push(item)
        }
      })
      this.select_arr = arr
      
      // 选中的名称数组
      this.select_name.map((item, index) => {
        if(index === shu) {
          arr2.push(li.spec_value)
        } else {
          arr2.push(item)
        }
      })
      this.select_name = arr2
      
      console.log('选中的规格id', arr, '选中的规格名称',  arr2)
      
      let id = this.select_arr.join('_')
      
      // 图片及价钱和库存
      let obj = this.goods
      this.specData.spec_list.map((item, index) => {
        if(item.spec_sku_id === id) {
          obj.goods_price= item.form.goods_price
          obj.stock_num= item.form.stock_num
          if(item.form.image_path) {
            obj.image_path = item.form.image_path
          }
        }
      })
      this.goods = obj
      
    },
    // 设置商品数量
    controlNum(type) {
      if(type === '+') {
        if(this.goods_num >= this.goods.stock_num) {
          uni.showToast({
            title: '库存不足',
            icon: 'none'
          })
          return
        }
        this.goods_num += 1
      }
      if(type === '-') {
        if(this.goods_num > 1) {
          this.goods_num -= 1
          return
        }
      }
    },
   
    //点击外面加入购物车
    outAddcar() {
      this.isCar = true
      this.normShow = true
    },
    
    // 加入购物车操作
    addCar() {
      console.log(this.goods_id, this.goods_num, this.select_arr.join('_'))  // goods_sku_id
      let that = this
      let data = {
        goods_sku_id: that.select_arr.join('_'),
        goods_id: that.goods_id,
        goods_num: that.goods_num
      }
      that.$http({
        url: that.$api.addcar,
        method: 'POST',
        data: data,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            uni.showToast({
              title: '添加成功',
              icon: 'none'
            })
            that.normShow = false
          } else if(res.code === 0 || res.code === -1 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '加入购物车失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 关闭商品规格弹窗
    closeNorm() {
      this.normShow = false
      this.isCar = false
    },
    
    // 返回
    goBack() {
      uni.navigateBack({
        delta: 1
        })
    },
    navAction(index) {
      this.navIndex = index
    },
    // 分享
    goShare() {
      uni.share({
        provider: "weixin",
        scene: "WXSceneSession",
        type: 0,
        href: "http://uniapp.dcloud.io/",
        title: "uni-app分享",
        summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
        imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
        success: function (res) {
          console.log('success:' + JSON.stringify(res));
        },
        fail: function (err) {
          console.log('fail:' + JSON.stringify(err));
        }
      })
    },
    // 收藏
    keepAction() {
      let that = this
      console.log('点击了收藏', that.detail.goods_iscollection, that.detail.goods_id)
      let iscollection = that.detail.goods_iscollection
      let data = {
        type: 'add',
        goods_id: that.detail.goods_id
      }
      if(iscollection === 'yes') {
        data.type = 'cancel'
      }
      that.$http({
        url: that.$api.goodscollection,
        data: data,
        cb: (err, res) => {
          console.log(res)
          if(that.$resFilter(err, res)) {
            that.detail.goods_iscollection = iscollection === 'yes' ? 'no': 'yes'
          }
        }
      })
    },
    // 页面滚动
    scroll(e) {
      this.top = e.detail.scrollTop
      if (e.detail.scrollTop > 375) {
        this.isShowTop = false
        return
      }
      if (e.detail.scrollTop <= 375) {
        this.isShowTop = true
        return
      }
    },
    // 点击促销信息
    lookInfo() {
      this.coverShow = true
    },
    goShopcar() {
      uni.switchTab({
        url: '../shopcar/shopcar'
      })
    }
  },
}
